<template>
   <div class="userAssets" v-loading="load" >

      <div class="menmuTitle flex align between">
        <h1>认购记录</h1>
      </div>
      <!-- menmuTitle -->

      <div class="listItem" v-if="list.length">
        <table>
          <thead>
            <tr>
              <th class="textLeft">认购额度 / 币种</th>
              <th class="textCenter">可获得/<small>WMC</small> </th>
              <th class="textCenter">已收益/<small>WMC</small></th>
              <th class="textCenter">认购时间</th>
              <th class="textCenter">状态</th>
              <th class="textRight">操作</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item,index) in list" :key="index">
              <td class="fromNumber">{{item.fromTotal}} / {{getCoinName(item.fromCoinId)}}</td>
              <td class="textCenter">{{item.totalCoin}}</td>
              <td class="textCenter">{{item.income}}</td>
              <td class="textCenter">{{item.startTime}}</td>
              <!--  / {{item.endTime}} -->
              <td class="textCenter">
                <el-tag size="mini" v-if="item.state=='STARTING'">释放中</el-tag>
                <el-tag type="info" size="mini" v-else>已结束</el-tag>
              </td>
              <td class="textRight"><div class="linkText" @click="gopage(`/userCenter/userOrderFreedList?id=${item.id}`)">释放记录</div></td>
            </tr>
          </tbody>
        </table>
      </div>
      <!-- assetsTable -->

       <div v-if="list.length==0" class="nullData"><span>暂无数据</span></div>

  </div>
</template>

<script>
  import {
    mapMutations,
    mapState,
    mapActions
  } from 'vuex'

  import {
    orderList 
  } from '../data/apis';

  
  
  export default {
    data() {
      return {
        list:[],
        load:false
      }
    },
    computed: {
      ...mapState(['userInfo'])
    },
    methods: {
      getCoinName(id){//获取币种名称
        return this.userInfo.balanceModels.find(x=>x.coin.id==id).coin.coinName;
      },//getCoinName
      getData(){//获取数据
        this.load = true;
        orderList().then(v=>{
          this.list = v.data;
          this.load = false;
        })
      }//getData
    },
    mounted() {
      this.getData();
    } //mounted
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">
  @import "../styles/Val";
  
</style>


